import { get } from 'lodash'

import { __ } from '@wordpress/i18n'
import { applyFormat, toggleFormat, removeFormat, getActiveFormat } from '@wordpress/rich-text'
import { Component, Fragment } from '@wordpress/element'
import { select, withSelect } from '@wordpress/data'
import { compose, ifCondition } from '@wordpress/compose'
import { BlockControls } from '@wordpress/block-editor'
import { Toolbar, IconButton, Popover, ColorPalette, ColorIndicator } from '@wordpress/components'
import { i18n } from 'frontgb'

const name = 'fgb/color'

class Edit extends Component {
    constructor( props ) {
        super( ...arguments )

        this.toggle = this.toggle.bind( this )

        this.state = {
            isOpen: false,
        }
    }

    toggle() {
        this.setState( ( state ) => ( {
            isOpen: ! state.isOpen,
        } ) )
    }

    render() {
        const { isOpen } = this.state
        const {
            value,
            onChange,
        } = this.props

        let activeColor

        const colors = get( select( 'core/block-editor' ).getSettings(), [ 'colors' ], [] )
        const activeColorFormat = getActiveFormat( value, name )

        if ( activeColorFormat ) {
            const styleColor = activeColorFormat.attributes.style
            if ( styleColor ) {
                activeColor = styleColor.replace( new RegExp( `^color:\\s*` ), '' )
            }
        }

        return (
            <Fragment>
                <BlockControls>
                    <Toolbar className="fgb-components-toolbar">
                        <IconButton
                            className="components-button components-icon-button components-fgb-toolbar__control components-toolbar__control components-fgb-color-format"
                            icon="editor-textcolor"
                            aria-haspopup="true"
                            tooltip={ __( 'Text Color', i18n ) }
                            onClick={ this.toggle }
                        >
                            <span
                                className="components-fgb-inline-color__indicator"
                                style={ {
                                    backgroundColor: activeColor,
                                } }
                            />
                        </IconButton>

                        { isOpen && (
                            <Popover
                                position="bottom center"
                                className="components-fgb__inline-color-popover"
                                focusOnMount="container"
                                onClickOutside={ ( onClickOutside ) => {
                                    if ( ( ! onClickOutside.target.classList.contains( 'components-fgb-color-format' ) && ! document.querySelector( '.components-fgb-color-format' ).contains( onClickOutside.target ) ) && ( ! document.querySelector( '.components-color-palette__picker' ) || ( document.querySelector( '.components-color-palette__picker' ) && ! document.querySelector( '.components-color-palette__picker' ).contains( onClickOutside.target ) ) ) ) {
                                        this.setState( { isOpen: ! isOpen } );
                                    }
                                } }
                            >
                                <ColorPalette
                                    colors={ colors }
                                    value={ activeColor }
                                    onChange={ ( color ) => {
                                        if ( color ) {
                                            onChange(
                                                applyFormat( value, {
                                                    type: name,
                                                    attributes: {
                                                        style: `color:${ color }`,
                                                    },
                                                } )
                                            )
                                        } else {
                                            onChange( removeFormat( value, name ) )
                                        }
                                    } }
                                >
                                </ColorPalette>
                            </Popover>
                        ) }

                    </Toolbar>
                </BlockControls>
            </Fragment>
        );
    }

}

export default compose(
    withSelect( select => {
        return {
            isDisabled: select( 'core/edit-post' ).isFeatureActive( 'disableEditorsKitColorsFormats' ),
        }
    } ),
    ifCondition( props => !props.isDisabled ),
)( Edit )