/**
 * Register all the formats found.
 *
 * This is the file that Webpack is compiling into editor_formats.js
 */
import { registerFormatType } from '@wordpress/rich-text'

const context = require.context( './format', true, /index\.js$/ )
const editContext = require.context( './format', true, /edit\.js$/ )

// Register all the formats found.
context.keys().forEach( key => {
    const format = context( key )

    const settings = {
        ...format.settings,
    }
    try {
        settings.edit = editContext( key.replace( 'index.js', 'edit.js' ) ).default
    } catch ( error ) {}

    if ( format.name ) {
        registerFormatType( format.name, settings )
    }
} )
