import { __ } from '@wordpress/i18n'
import { toggleFormat } from '@wordpress/rich-text'
import { Fragment } from '@wordpress/element'
import { RichTextShortcut, RichTextToolbarButton } from '@wordpress/block-editor'
import { FrontGBIcon } from '@frontgb/icons'
import { i18n } from 'frontgb'

const name = 'fgb/animation'

const edit = props => {
    const { isActive, value, onChange } = props
    const onToggle = () => onChange( toggleFormat( value, { type: name } ) );

    return (
        <Fragment>
            <RichTextShortcut
                type="access"
                character=";"
                onUse={ onToggle }
            />
            <RichTextToolbarButton
                icon={ FrontGBIcon }
                title={ __( 'Animation', i18n ) }
                onClick={ onToggle }
                isActive={ isActive }
                shortcutType="access"
                shortcutCharacter=";"
            />
        </Fragment>
    )
}

export default edit